.pageLayout{
    display: flex;
    width: 100%;
}

.users{
    border: rgb(67, 8, 118);
    width: 77%;
    max-width: 77%;
    border-radius: 13px;
    background-color: #A679CA;
    box-shadow: 1px 2px 10px 2px #7f5d9c;
}
.user{
    border-radius: 13px;
    padding: 20px;
    background-color: #A679CA;
}

.filterUsers{
    padding: 10px;
    border: rgb(67, 8, 118);
    width: 20%;
    border-radius: 13px;
    background-color: #A679CA;
    margin-left: 10px;
    box-shadow: 1px 2px 10px 2px #7f5d9c;
}
.filterUsers .item{
    border-radius: 7px;
    cursor: pointer;
    padding: 10px; 
}
.filterUsers .active{
    border-radius: 7px;
    cursor: pointer;
    padding: 10px; 
    background-color: #9e6ec5;
}